.feed_wrapper {
  text-align: left;
}

.feed_wrapper > section {
  display: flex;
  gap: 60px;
}
.feed_wrapper > section > * {
  flex: 1;
  max-height: calc(100vh - 250px);
  overflow: auto;
}


.order_numbers_wrapper {
  display: flex;
  gap: 36px;
}

.order_numbers_wrapper > * {
  flex: 1;
}

.order_numbers {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 8px;
  max-height: 320px;
  overflow: auto hidden;
}

.order_numbers > * {
  flex: 1 0 20%;
}
